.App {
  text-align: center;
  /* background-color: lightgrey; */
  background-image: linear-gradient(transparent,transparent, lightgrey)
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

body{
  font-family: "Brush Script MT",cursive;
}

.line {
  width: 95vw;
  height: 5px;
  border-radius: 5px;
  background-color: grey;
  margin: 20px auto;
  position: relative;
  top: -7.5px;
  border-style: groove;

  width: 95vw;
  height: 6px;
  border-radius: 5px;
  /* background-color: grey; */
  margin: 20px auto;
  position: relative;
  top: -7.5px;
  /* border-style: dotted; */
  width: 90%;
  height: 5px;
  border-top: 7px outset lightgrey;
  line-height: 80%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

a {
  text-decoration: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

